.mydiv {
    spacing: 2px;
    padding: 20px 2px 2px 2px;
    width: 100%;
}


.flexbox {
  float: left;
  width: 100%;
  background-color: #FEF1E0;
}
* {
  box-sizing: border-box;
}

.menu {
  float: left;
  width: 20%;
  text-align: center;
}

.menu a {
  background-color: #FEF1E0;
  padding: 8px;
  margin-top: 7px;
  display: block;
  width: 100%;
  color: black;
}

.main {
  float: left;
  width: 60%;
  padding: 0 20px;
  background-color: #FEF1E0;
}

.right {
  background-color: #FEF1E0;
  float: left;
  width: 20%;
  padding: 15px;
  margin-top: 7px;
  text-align: center;
}
.mycard {
    background-color: #FEF1E0;
    width: 25%;
    float: left;
}

@media only screen and (max-width: 700px) {
  /* For mobile phones: */
  .menu, .main, .right, .mycard {
    width: 100%;
  }
}
